.promo {
  min-height: 100vh;
  position: relative;
  background: url('../../img/compblack.jpeg') no-repeat 50%/cover;
  padding-top: 205px;
  &__title {
    width: 535px;
    margin-top: 10px;
  }
  &__subtitle {
    position: relative;
    padding-left: 10px;
    z-index: 2;
    @include before_circle;
  }
  &__btns {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 250px;
  }
  &__link {
    color: #000;
    font-weight: bold;
    &:hover {
      transition: all .3s;
      color:$main-color;
      text-decoration: none;
    }
  }
}